.list-lecture {
  h2 {
    font-size: 18px;
    color: $bg-primary-color;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 20px;
    margin-top: 10px;
  }
  table {
    border: none;
    border-radius: 6px;
    border-bottom: 3px solid $bg-primary-color;
  }
  thead {
    background-color: $bg-primary-color;
    border-radius: 6px 6px 0px 0px;
    tr {
      border-radius: 6px;
    }
    th {
      color: #fff;
      font-weight: 400;
      border-radius: 6px 6px 0px 0px;
      border: none;
      border-right: 1px solid #fff;
      font-size: 14px;
    }
    th:last-child {
      border-right: none;
    }
  }
  tbody {
    tr:nth-of-type(odd) {
      background: #fff;
    }
    tr:nth-of-type(even) {
      background: #fdf1f1;
    }
    tr {
      th,
      td {
        border: none;
        border-right: 1px solid #f4b2af;
      }
      th:last-child,
      td:last-child {
        border-right: transparent;
      }
    }
  }
  h3,
  h6 {
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    margin: 0px;
  }
  a {
    transition: 0.5s;
    color: $bg-primary-color;
  }
  a:hover {
    color: #000;
  }
  th {
    font-size: 13px;
  }
}
.lecture-item-content {
  header {
    h1 {
      font-size: 16px;
