.question {
  .card {
    margin-bottom: 30px;
    .card-header {
      padding: 10px 20px;
      background-color: #f5f5f5;
    }
    .form-group {
      padding: 0px;
    }
    .form-row {
      margin-left: 0px;
      margin-right: 0px;
    }
    h3 {
      font-size: 16px;
      margin-bottom: 0px;
      line-height: 22px;
      font-weight: 700;
      color: $bg-primary-color;
    }
  }
  form {
    .form-control {
      font-size: 14px;
      line-height: 20px;
      color: #000;
    }
    button {
      font-weight: 400;
      font-size: 14px;
      padding: 7px 15px;
      max-height: 35px;
      transition: 0.5s;
    }
    button:hover {
      background: $bg-secondary-color;
      border-color: $bg-secondary-color;
    }
  }

  .accordion {
    li {
      border: 1px solid rgba(0, 0, 0, 0.125);
      padding: 1.25rem;
      border-radius: 4px;
      margin-bottom: 15px;
      .title {
        color: $bg-primary-color;
        font-weight: 600;
        margin-bottom: 10px;
      }
      li {
        border: none;
        padding: 0px;
        margin: 0px;
      }
      span {
        display: block;
      }
      .act {
        color: #888;
        transition: 0.5s;
        i {
          width: 15px;
        }
      }
      .act:hover {
        color: $bg-secondary-color;
      }
      .inner {
        margin-top: 15px;
        padding-left: 20px;
        margin-left: 20px;
        border-left: 3px solid #ddd;
        .title {
          color: #007bff;
        }
        p {
          line-height: 20px;
        }
      }
    }
  }
