}

.mutilmedia {
  padding-top: 0px !important;
  .tabs {
    list-style: none;
    display: inline-block;
    padding-left: 0px;
    border-bottom: 1px solid #bdbdbd;
    padding-bottom: 10px;
    margin-bottom: 20px;
    li {
      display: inline-block;
      margin-right: 30px;
      position: relative;
      a {
        font-weight: 700;
        font-size: 18px;
        color: #828282;
        text-transform: uppercase;
        line-height: 24px;
        position: relative;
      }
      a:after {
        position: absolute;
        bottom: -12px;
        width: 80px;
        height: 2px;
        content: "";
        left: 0;
      }
      .active:after,
      a:hover:after {
        background-color: $bg-primary-color;
      }
      .active,
      a:hover {
        color: #000;
      }
    }
    li:first-child:before {
      content: "";
      position: absolute;
      right: -20px;
      width: 2px;
      height: 20px;
      background-color: #828282;
      top: 2px;
    }
  }
  li:last-child {
    margin-right: 0px;
  }
  .col-md-6:last-child {
    padding-left: 3px;
  }
  .col-md-6:first-child {
    padding-right: 3px;
  }
  .slider-for {
    .item {
      height: 326px;
      position: relative;
      overflow: hidden;
      .title {
        position: absolute;
        bottom: 0px;
        background: rgba(0, 0, 0, 0.54);
        h3 {
          font-size: 16px;
          padding: 15px;
          color: #fff;
          margin: 0px;
          font-weight: 400;
          line-height: 22px;
        }
      }
      img {
        width: 100% !important;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  #video {
    .rvs-nav-container {
      .rvs-nav-item h4 {
        font-size: 14px;
        color: #fff !important;
      }
      .rvs-nav-item {
        background-color: #333333;
        border-color: #555;
      }
      .rvs-active,
      .rvs-nav-item:hover {
        background-color: $bg-primary-color;
      }
      .rvs-nav-item:hover h4 {
        color: #fff !important;
      }
    }
    .rvs-container a.rvs-play-video:hover {
      border-color: $bg-primary-color;
      background-color: $bg-primary-color;
    }
  }
}

.question {
  ul {
    list-style: none;
    padding: 0;

    .inner {
      padding-left: 1em;
      overflow: hidden;
      display: none;

      &.show {
        /*display: block;*/
      }
    }

    li {
      margin: 0.5em 0;
    }
  }
}

.list-social {
  .list-share {
    display: inline-block;
    .zalo-share-button {
      margin-bottom: -5px;
    }
  }
}
